"use client";

import { useEffect, useState } from "react";

interface SyncData {
  dayOfYear: number;
  lunaNum: number;
  dayInLuna: number;
  lunaName: string;
}

const lunaNames: string[] = [
  "Luna Magnética",
  "Luna Lunar",
  "Luna Eléctrica",
  "Luna Autoexistente",
  "Luna Entonada",
  "Luna Rítmica",
  "Luna Resonante",
  "Luna Galáctica",
  "Luna Solar",
  "Luna Planetaria",
  "Luna Espectral",
  "Luna Cristal",
  "Luna Cósmica",
];

export default function Sincronario() {
  const [sync, setSync] = useState<SyncData>({
    dayOfYear: 0,
    lunaNum: 0,
    dayInLuna: 0,
    lunaName: "",
  });

  useEffect(() => {
    const today = new Date();

    // Día del año (1–365)
    const start = new Date(today.getFullYear(), 0, 0);
    const diff = today.getTime() - start.getTime();
    const oneDay = 1000 * 60 * 60 * 24;
    const dayOfYear = Math.floor(diff / oneDay);

    // Cada luna tiene exactamente 28 días
    const lunaNumRaw = Math.ceil(dayOfYear / 28);
    const lunaNum = lunaNumRaw > 13 ? 13 : lunaNumRaw;

    const dayInLuna = ((dayOfYear - 1) % 28) + 1;

    const lunaName = lunaNames[lunaNum - 1];

    setSync({
      dayOfYear,
      lunaNum,
      dayInLuna,
      lunaName,
    });
  }, []);

  return (
    <div style={styles.card}>
      <h2>Sincronario de 13 Lunas</h2>

      <div style={styles.row}>
        <strong>Luna:</strong> {sync.lunaNum} – {sync.lunaName}
      </div>

      <div style={styles.row}>
        <strong>Día en la Luna:</strong> {sync.dayInLuna} / 28
      </div>

      <div style={styles.row}>
        <strong>Día del Año:</strong> {sync.dayOfYear}
      </div>
    </div>
  );
}

const styles: Record<string, React.CSSProperties> = {
  card: {
    background: "#111",
    padding: "20px",
    borderRadius: "12px",
    color: "white",
    width: "300px",
    border: "1px solid #333",
    fontFamily: "sans-serif",
  },
  row: {
    marginBottom: "10px",
  },
};
